import * as React from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import { useRequest } from '@miq/hookjs';
import { getCN, Response } from '@miq/utiljs';
import { Pagination, Table } from '@miq/componentjs';

import { TSupplierOrder } from '../types';

import { SupplierOrder } from './models';
import { SupplierOrderAddButton, SupplierOrderDeleteButton, SupplierPrice } from './components';

const SupplierOrderRow = ({ item, removeItem }: { item: TSupplierOrder; removeItem: (payload: any) => void }) => {
  const { name = 'New order', slug, is_paid, items, items_revenue, supplier } = item || {};

  return (
    <Table.Tr>
      <Table.Td>
        <Staff.Link to={`${slug}/`} className="">
          <div className={getCN(['fw-bold text-ellipsis', is_paid && 'text-line-through'])}>{name}</div>
        </Staff.Link>
        <span className="text-sm">{item.avg_cost}</span>
      </Table.Td>

      <Table.Td>{items?.length}</Table.Td>

      <Table.Td>{supplier}</Table.Td>

      <Table.Td>
        <SupplierPrice {...item.total_cost_data} />
      </Table.Td>
      <Table.Td>
        <SupplierPrice {...items_revenue} />
      </Table.Td>

      <Table.Td>
        <SupplierOrderDeleteButton instance={SupplierOrder(item)} onSuccess={() => removeItem?.({ slug })} />
      </Table.Td>
    </Table.Tr>
  );
};

export default function SupplierOrderIndexView() {
  const navigate = useNavigate();
  const [params] = useSearchParams();
  const page = params.get('page');

  const { res, removeItem } = useRequest(() => SupplierOrder().list({ params: { page } }), { refreshDeps: [page] });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const { items } = r;

  return (
    <Staff.View
      title="Supplier Orders"
      back="/staff/store/"
      actions={
        <SupplierOrderAddButton onSuccess={({ data }: any) => navigate(`${data.slug}/`)} className="btn-primary-3" />
      }
      requiredPerms={['store.view_supplierorder']}
    >
      {Boolean(items.length) ? (
        <Staff.Section footer={<Pagination {...r.data} component={Staff.Link} to />}>
          <Table
            border
            items={items}
            header={
              <>
                <Table.Th />
                <Table.Th>Items</Table.Th>
                <Table.Th>Supplier</Table.Th>

                <Table.Th>Total Cost</Table.Th>
                <Table.Th>Est. Revenue</Table.Th>
                <Table.Th />
              </>
            }
            renderItem={(item) => <SupplierOrderRow {...{ item, removeItem }} key={item.slug} />}
            className="w-100"
          />
        </Staff.Section>
      ) : (
        <div className="p-4">
          <div className="text-center">
            <div className="mb-4 text-md">Add a supplier order to get started.</div>
            <SupplierOrderAddButton
              onSuccess={({ data }: any) => navigate(`${data.slug}/`)}
              label="Add an order"
              className="btn-md btn-primary fw-bold"
            />
          </div>
        </div>
      )}
    </Staff.View>
  );
}

// EXPORTS

export * from './models';
