import * as React from 'react';

import Staff from '@miq/staffjs';
import { truncateStr } from '@miq/utiljs';
import { ProductPriceDisplay, TProduct, TProductSize } from '@shopy/storejs';

import { TCustomer } from './types';

export const ItemProductCard = ({ product, size }: { product: TProduct; size: TProductSize }) => (
  <div className="d-flex align-items-center">
    <ProductImage {...{ product, width: 60, height: 60 }} />
    <div className="flex-1 rounded p-1 ">
      <div className="fw-bold">{truncateStr(product?.name, 0, 25)}</div>
      <ProductPriceDisplay {...product} inline />
      {size && (
        <div>
          <span className="me-1">Size:</span>
          <span className="text-sm">{size?.name}</span>
        </div>
      )}
    </div>
  </div>
);

export const ProductImage = ({
  product,
  width,
  height,
  ...props
}: {
  product: TProduct;
  width?: number | string;
  height?: number | string;
}) =>
  product?.slug ? (
    <div {...props} style={{ width: width, height: height }}>
      <Staff.Img.Square slug="slug" src="src" {...product.cover_data} className="rounded" />
    </div>
  ) : null;

export const CustomerCard = ({ data, ...props }: { data: TCustomer }) => {
  if (!data || !data.slug) return null;

  const { first_name, last_name, email, phone } = data;

  return (
    <div className="customer-card">
      <div className="fw-bold">{`${first_name} ${last_name}`}</div>
      <div className="">{phone}</div>
      <div className="text-muted text-sm">{email}</div>
    </div>
  );
};
