import * as React from 'react';

import { Button, ConfirmButton, Icons } from '@miq/componentjs';
import { IFormUpdateProps } from '@miq/formjs';
import { getCN } from '@miq/utiljs';
import { SupplierOrder, TSupplierOrderInstance } from './models';
import Staff from '@miq/staffjs';

type TSupplierOrderDeleteButtonProps = Omit<IFormUpdateProps<TSupplierOrderInstance>, 'context'> & {
  label?: string;
};

export const SupplierOrderDeleteButton: React.FC<TSupplierOrderDeleteButtonProps> = (props) => {
  const { instance, onSuccess, onError, label } = props;

  const handleDelete = (setOpen: any) => {
    instance
      .destroy()
      .then((res) => {
        setOpen?.(false);
        onSuccess?.(res);
        // emitter.destroy({ slug: instance.slug });
      })
      .catch((err) => onError?.(err));
  };

  return (
    <ConfirmButton
      renderHeader={() => <div>Supprimer '{instance.name}'</div>}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button onClick={() => handleDelete(setOpen)} className="btn-danger">
            Supprimer
          </Button>
        </div>
      )}
      render={() => <div className="p-3">Are you sure you want to delete this</div>}
      className={getCN(['s-order-delete-button btn-danger-3', props.className])}
    >
      <Icons.Trash />
      {label && <span className="label">{label}</span>}
    </ConfirmButton>
  );
};

export const SupplierOrderAddButton = (props: { onSuccess: (p?: any) => void; label?: string; className?: string }) => {
  const { label = 'Add order' } = props;

  return (
    <Staff.Button
      onClick={() => {
        SupplierOrder()
          .create({})
          .then((r) => props?.onSuccess?.(r))
          .catch((err: any) => {
            // console.log(err);
          });
      }}
      className={props.className}
      requiredPerms={['store.add_supplierorder']}
      title={'Add a new order'}
    >
      {label}
    </Staff.Button>
  );
};

export const SupplierPrice = (props: any) => {
  const { amountWithSymbol } = props;
  return <div className="miq-price">{amountWithSymbol}</div>;
};
