//
//

export const ProductListFilterForm = ({
  children,
  ...props
}: {
  children?: React.ReactNode;
  categories?: CategoryChoicesType;
}) => {
  const [params, setParams] = useSearchParams();
  const [q, setQ] = React.useState<string>(params.get('q') || '');
  const { categories, ...rest } = props;

  return (
    <form
      {...rest}
      action="."
      method="GET"
      onSubmit={(e) => {
        e.preventDefault();

        if (!q) params.delete('q');
        else params.set('q', q);
        params.delete('page');

        setParams(params);
      }}
    >
      {children || (
        <>
          <div className="mb-2 d-flex">
            <input
              type="text"
              name="q"
              value={q}
              onChange={(e) => {
                const { value } = e.target;
                setQ(value);
                if (!value && params.get('q')) params.delete('q');
              }}
              className="miq-form-input flex-1"
              placeholder={i18n('ListFilterForm.Q.placeholder')}
            />
            <Form.Submit value={i18n('ListFilterForm.submitLabel')} className="btn-primary-3 ms-1" />
          </div>

          {categories && <ProductListFilterForm.Category {...{ params, setParams, categories }} />}

          <ProductListFilterForm.Published {...{ params, setParams }} className="mb-1" />
          <ProductListFilterForm.Sale {...{ params, setParams }} className="mb-1" />
          <ProductListFilterForm.PreSale {...{ params, setParams }} className="mb-1" />
        </>
      )}
    </form>
  );
};

type TParamsProps = {
  params: URLSearchParams;
  setParams: (
    nextInit: URLSearchParamsInit,
    navigateOptions?:
      | {
          replace?: boolean | undefined;
          state?: any;
        }
      | undefined
  ) => void;
};

type QuerySelectFieldProps = TParamsProps & {
  emptyLabel?: string;
  name: string;
  children?: React.ReactNode;
  className?: string;
};

ProductListFilterForm.Category = (props: Omit<QuerySelectFieldProps, 'name'> & { categories: CategoryChoicesType }) => {
  const { categories, ...rest } = props;

  return (
    <QuerySelectField emptyLabel={i18n('ListFilterForm.categoryEmptyLabel')} {...rest} name="cat">
      <option value="no-cat">{i18n('ListFilterForm.noCategoryOptionLabel')}</option>

      {categories.items.map((cat) => {
        return (
          <option value={cat.value} key={cat.slug}>
            {cat.label}
          </option>
        );
      })}
    </QuerySelectField>
  );
};

ProductListFilterForm.Published = (props: Omit<QuerySelectFieldProps, 'name'>) => {
  return (
    <QuerySelectField emptyLabel={i18n('ListFilterForm.publishedEmptyLabel')} {...props} name="published">
      <option value="include">{i18n('ListFilterForm.publishedOptionLabel')}</option>
      <option value="pinned"> {i18n('ListFilterForm.pinnedOptionLabel')}</option>
      <option value="exclude"> {i18n('ListFilterForm.unpublishedOptionLabel')}</option>
      <option value="explicit"> {i18n('ListFilterForm.explicitOptionLabel')}</option>
    </QuerySelectField>
  );
};

const QuerySelectField = ({ children, ...props }: QuerySelectFieldProps) => {
  const { name, params, setParams, emptyLabel } = props;
  return (
    <select
      className={getCN(['miq-select', props.className])}
      name={name}
      value={params.get(name) || ''}
      onChange={(e) => {
        const { value } = e.target;

        if (!value) params.delete(name);
        else params.set(name, value);
        params.delete('page');

        setParams(params);
      }}
    >
      {emptyLabel && <option value="">{emptyLabel}</option>}
      {children}
    </select>
  );
};

ProductListFilterForm.PreSale = (props: Omit<QueryToggleFieldProps, 'name' | 'label'>) => {
  return <QueryToggleField {...props} name="presale" label={i18n('ListFilterForm.preSaleLabel')} />;
};

ProductListFilterForm.Sale = (props: Omit<QueryToggleFieldProps, 'name' | 'label'>) => {
  return <QueryToggleField {...props} name="sale" label={i18n('ListFilterForm.onSaleLabel')} />;
};

type QueryToggleFieldProps = Omit<QuerySelectFieldProps, 'emptyLabel'> & { label: string; inputId?: string };

const QueryToggleField = (props: QueryToggleFieldProps) => {
  const { label, name, params, setParams, inputId } = props;

  return (
    <div className={getCN(['miq-query-filter-toggle d-flex align-items-center', props.className])}>
      <input
        type="checkbox"
        className="miq-checkbox me-1"
        id={inputId}
        onChange={(e) => {
          const { checked } = e.target;
          if (!checked) params.delete(name);
          else params.set(name, `${checked}`);
          params.delete('page');
          setParams(params);
        }}
        checked={Boolean(params.get(name)) || false}
      />
      <label htmlFor={inputId}>{label}</label>
    </div>
  );
};
